.App {
  /* text-align: center; */
  max-width: 1400px;
  width: 100%;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
}

:root{
  --main-green: #234d4c;
}

body{
  /* background-color: #dee1e6; */
  /* font-family: 'Poppins', sans-serif; */
  font-family: 'Noto Serif JP', serif;
}

.App-header {
  /* background-color: #282c3415; */
  /* background-color: rgba(155, 127, 127, 0.301); */
  /* min-height: 100vh; */


  /* align-items: center; */
  /* justify-content: center; */
  /* font-size: calc(10px + 2vmin); */
  color: var(--main-green);

}

ul{
  list-style: none;
}

li{
  text-decoration: none;
}

li:hover{
  cursor: pointer;
}


/* ---------------- NAV BAR -------------------- */
.navmenu{
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 80px;
  /* background-color: antiquewhite; */
  color: var(-main-green);
  padding: 0 100px;
}

.logo{
  width: 20%;
  font-size: 26px;
  font-weight: 600;
}

.nav-menu-items{
  width: 50%;
}

.nav-menu-items ul{
  display: flex;
}

.nav-menu-items ul li{
  padding: 0 60px 0 0;
  font-weight: 600;
  font-size: 16px;
  color: #242424;
}

.nav-menu-navigation ul{
  display: flex;
}

.nav-menu-navigation ul li:nth-child(1){
  padding: 0 60px 0 0;
}


/* ----------------- HERO ------------------ */

.hero-section{
  position: relative;
  /* background-color: red; */
  /* height: calc(70vh - 80px); */
  height: 824px;
  clear: both;
}

.hero-main-image{
  text-align: right;
  object-fit: contain;
  width: 100%;
  height: 100%;
  /* padding: 20px 0; */
  /* overflow: hidden; */
}

.hero-image{
  /* background-color: blue; */
  width: 991px;
  /* max-width: 70%; */
  /* max-width: 80%; */
  max-height: 100%;
  padding: 0 100px 0 0;
}

/* .hero-image img{
  width: 50%;
} */



.hero-message{
  width: 535px;
  height: 470px;
  background-color:rgba(255, 255, 255, 0.502);
  position: relative;
  top: -617px;
  left: 0px;
  /* padding: 30px; */
  color: var(--main-green);
  backdrop-filter: blur(8px);
  text-align: left;
}

.hero-message h1{
  font-size: 2.5rem;
  padding: 72px 101px 0 97px;
  font-weight: 900;
}

.hero-message p{
  padding: 0 160px 0 97px;
  font-weight: 400;
}

.hero-more{
  width: 124px;
  height: 124px;
  background-color: rgba(238, 238, 238, 0.742);
  backdrop-filter: blur(3px);
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  right: -1190px;
  bottom: 610px;
}

.hero-more .fa-arrow-down{
  font-size: 2.5rem;
}

.hero-side-menu{
  /* background-color: #f8bb82; */
  position: relative;
  right: -800px;
  top: -700px;
  width: 100px;
  height: 90%;
  display: flex;
  flex-direction: column;
}

.hero-arrows,
.hero-numbers{
  /* background-color: red; */
  height: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
}

.hero-arrows .fa-solid{
  font-size: 12px;
  padding: 10px 0;
}

.hero-numbers{
  font-size: 12px;
  font-weight: 600;
  color: #000;
  display: flex;
  align-items: center;
  margin: 0;
}

.hero-numbers .line{
  border-left: 1px solid #000;
  height: 40px;
}



/* -------------- SPONSORS -------------- */
.sponsors-section{
  background-color: #F4F6F6;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 205px;
  /* margin: 50px 0; */
}

.sponsors-section h3{
  color: #031745;
}

.sponsor-list ul{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30px;
}

.sponsor-list img{
  width: 100px;
  display: flex;
  padding-right: 80px;
}


/* ---------------- DEALS ------------- */
.deals-section{
  height: 440px;
  /* background: rgb(244, 166, 166); */
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 80px
}

.deal-title{
  padding-right: 80px;
}

.deals-section h1{
  font-size: 50px;
  color: #244D4D;
  margin: 0 0;
  line-height: 1;
}

.deal-title p{
  font-size: 16px;
  color: #5F5F64;
  width: 250px;
}

.deal-section{
  padding-right: 100px;
}

.deal-section p{
  font-size: 16px;
  color: #5F5F64;
  width: 200px;
}



/* ------------------ ABOUT US ----------------- */
.about-section{
  height: 900px;
  /* background-color: #5a86eb8f; */
  /* display: flex;
  justify-content: space-between;
  align-items: center; */
  padding: 0 80px;

}

.about-header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 200px;
}

.about-title{
  width: 450px;
}

.about-btn{
  height: 80px;
}

.about-title h1{
  font-size: 50px;
  margin: 0 0;
}

.about-title p{
  width: 390px;
  color: #64717C;
  line-height: 2;
}


.about-list{
  /* background-color: #98edf84c; */
  height: 700px;
  width: 700px;
  display: flex;
}

.about-row-1,
.about-row-2{
  display: flex;
}

.about{
  width: 290px;
  padding-right: 60px;
}

.about h1{
  font-size: 58px;
}

.about h3{
  font-size: 24px;
}

.about p{
  font-weight: 400;
}

.about-display{
  display: flex;
  align-items: center;
}


/* --------------- PRODUCTS ------------- */
.product-section{
  /* height: 1300px; */
  /* background-color: #fde9bcd7; */
}

.product-heading{
  /* background-color: #a5c0ffa4; */
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 20px;
  padding: 80px 130px;
}

.product-title h1{
  font-size: 50px;
}

.product-more{
  display: flex;
  align-items: center;
  justify-content: center;
}

.product-more p{
  color: #3D3D3F;
  font-size: 17px;
  padding-right: 25px;
}

.product-filter ul{
  display: flex;
  margin: 0 auto;
  justify-content: center;
}

.product-filter ul li{
  padding: 0 60px 0 0;
  color: #A9A7A6;
}

.product-filter ul li:nth-child(1){
  color: #3D3D3F;
}




.product-shop{
  /* background-color: #bffff5ba; */
  /* width: 1049px; */
  /* height: 920px; */
  display: flex;
  margin: 0 auto;
  margin-top: 100px;
}


.shop-list{
  padding: 0 70px;
  display: flex;
  /* margin: 0 auto; */
  flex-wrap: wrap;
}






.shop{
  width: 315px;
  height: 440px;
  padding-right: 52px;
  /* background-color: #fdade06c; */
}

.item-background{
  background-color: rgba(233, 233, 233, 0.471);
  height: 260px;
}

.item-background h2{
  font-size: 26px;
  color: #3D3D3F;
  margin: 10px 0 0;
  text-align: left;
  padding-left: 50px;
}

.shop-row{
  display: flex;
  /* margin: 0 auto; */
  text-align: center;
}

.item-background{
  padding: 30px 0;
  margin: 20px 0;

}

.item-background img{
  padding: 60px 0 0px;
}

.shop-price{
  display: flex;
  align-items: center;
  margin: 0;
}

.shop p{
  text-align: left;
  padding-left:50px;
  color: #3D3D3F;
  font-size: 19px;
}

.shop p:nth-child(2){
  text-decoration: line-through;
  color: #A9A7A6;
  font-size: 15px;
}





/* ------------------ FEEDBACK SECTION ------------------- */
.feedback-section{
  background-color: #F4F6F6;
  /* margin-top: 100px; */
  height: 980px;
  width: 1260px;
  margin: 100px auto 0;
  border-radius: 10px;
}

.feedback-header{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.feedback-heading{
  text-align: center;
  width: 475px;
}

.elipse-lg{
  position: relative;
  top: -31px;
}

.feedback-heading h1{
  padding-top: 58px;
  font-size: 58px;
  line-height: 1.3;
}

.feedback-heading p{
  line-height: 1.8;
}

.icon{
  padding-right: 170px;
  padding-top: 200px;
}


.feedback-client{
  display: flex;
  margin-top: 80px;
}

.client-image{
  /* background-color: #628cec8f; */
  width: 40%;
  display: flex;
  justify-content: center;
  padding-left: 100px;
}

.client-testimonial{
  /* background-color: #fbee8e9c; */
  width: 35%;
  padding: 0 50px 0 0px;
}

.client-testimonial h2{
  font-size: 26px;
  color: #244D4D;
  font-weight: bold;
}

.client-testimonial p:nth-child(2){
  font-size: 24px;
  color: #5E7388;
  font-style: italic;
  font-weight: 500;
}

.client-testimonial h2:nth-child(3){
  color: #152137;
  font-size: 22px;
  padding-top: 30px;
}

.client-testimonial p:nth-child(4){
  color: #647383;
  font-size: 14px;
}

.arrows{
  padding-top: 30px;
}

.right-arrow{
  padding-right: 20px;
  transform: scaleX(-1);
}



/* -------------- FOOTER -------------- */
.footer-section{
  margin-top: 80px;
  background-color: #244D4D;
  height: 730px;
  /* padding-left: 140px; */
  padding-top: 130px;
}

.footer-heading{
  display: flex;
  align-items: center;
}

.footer-heading h1{
  color: #fff;
  font-size: 45px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 400;
  width: 45%;
  padding-right: 50px;
  padding-left: 140px;
  line-height: 1.3;
}

.footer-buttons img:nth-child(1){
  padding-right: 20px;
}


.footer-divider img{
  padding: 60px 0px 0 100px;
}


.footer-bottom{
  display: flex;
  justify-content: space-between;
  padding: 0 150px;
  margin-top: 90px;
  line-height: 2;
}

.footer-col-1{
  max-width: 230px;
  width: 100%;
  /* background-color: #83a8ff8d; */
}

.footer-col-1 h1,
.footer-col-1 p{
  color: #fff;
}

.footer-col-1 h1{
  font-size: 37px;
  margin-top: 15px;
  margin-bottom: 0;
}

.footer-col-1 p{
  font-size: 15px;
  padding-bottom: 10px;
}

.footer-col-1 img{
  padding-right: 25px;
  cursor: pointer;
}

.footer-col li{
  text-decoration: none;
  text-align: left;
  font-size: 15px;
  font-weight: 300;
}

.footer-col ul{
  padding: 0;
}

.footer-col h3{
  font-size: 17px;
}

.footer-col-2,
.footer-col-3,
.footer-col-4,
.footer-col-5{
  /* background-color: #d804554a; */
  max-width: 170px;
  width: 100%;
  color: #fff;
  display: flex;
  flex-direction: column;
  /* align-items: center; */
}









/* ------------------ MEDIA QUERIES -------------------- */
@media only screen and (max-width: 1300px) {
  .nav-menu-items{
    display: none;
  }

  .hero-more{
    display: none;
  }
}



@media only screen and (max-width: 1100px) {
  .logo{
    display: none;
  }

  .navmenu{
    justify-content: end !important;
    padding: 0 62px;
  }

  .hero-section{
    height: calc(55vh - 80px);
  }

  .hero-main-image{
    text-align: center;
  }

  .hero-image{
    /* padding-right: 50px; */
    padding: 0;

  }

  .hero-message{
    padding: 20px;
    top: -400px;
    left: 365px;
    text-align: center;
  }
}


@media only screen and (max-width: 1000px){
  .hero-main-image{
    text-align: center !important;
  }

  .hero-image{
    padding-right: 0;
    width: 80%;
  }

  .hero-message{
    display: none;
    /* flex-direction: column;
    margin: 20px auto;
    padding: 50px;
    position: static; */
  }

  .hero-message h1{
    font-size: 1.5rem;
    text-align: center;
  }

  .hero-message p{
    font-size: 0.8rem;
    text-align: center;
  }
}




